<template><section>
   
<h2 id="input-shu-ru-kuang"><a class="header-anchor" href="#input-shu-ru-kuang" aria-hidden="true">¶</a> Input 输入框</h2>
<p>通过鼠标或键盘输入字符</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<docs-demo-block> 
                                                <div class="source" slot="source">    <fm-input v-model="val"></fm-input> 
        
</div>
                                                <p>基础的输入框用法。</p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span> 
    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
        <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
            data () {
                <span class="hljs-keyword">return</span> {
                    <span class="hljs-attr">val</span>: <span class="hljs-string">''</span>
                }
            }
        }
    </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>    
</code></pre>
</docs-demo-block>
<h3 id="jin-yong-zhuang-tai"><a class="header-anchor" href="#jin-yong-zhuang-tai" aria-hidden="true">¶</a> 禁用状态</h3>
<docs-demo-block> 
                                                <div class="source" slot="source">    <fm-input v-model="val" :disabled="true"></fm-input>   
</div>
                                                <p>通过 <code>disabled</code> 属性指定是否禁用 input 组件</p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"true"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<h3 id="chi-cun"><a class="header-anchor" href="#chi-cun" aria-hidden="true">¶</a> 尺寸</h3>
<docs-demo-block> 
                                                <div class="source" slot="source">    <fm-input placeholder="默认大小"></fm-input>   
    <fm-input placeholder="最大尺寸" size="large"></fm-input>
    <fm-input placeholder="小尺寸" size="small"></fm-input>
    <fm-input placeholder="最小尺寸" size="mini"></fm-input>
</div>
                                                <p>可通过 <code>size</code> 属性指定输入框的尺寸，默认大小是 <code>medium</code></p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"默认大小"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>   
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最大尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"小尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"最小尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"mini"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="ke-qing-kong"><a class="header-anchor" href="#ke-qing-kong" aria-hidden="true">¶</a> 可清空</h3>
<docs-demo-block> 
                                                <div class="source" slot="source">    <fm-input clearable="" v-model="val1"></fm-input>
       
</div>
                                                <p>可通过 <code>clearable</code> 属性得到一个可清空的输入框</p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">clearable</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"val1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
        <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
            data () {
                <span class="hljs-keyword">return</span> {
                    <span class="hljs-attr">val1</span>: <span class="hljs-string">'点击按钮清空'</span>
                }
            }
        }
    </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<h3 id="fu-he-xing-shu-ru-kuang"><a class="header-anchor" href="#fu-he-xing-shu-ru-kuang" aria-hidden="true">¶</a> 复合型输入框</h3>
<p>可前置或后置元素，一般为标签或按钮</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><div class="input-demo">
  <fm-input placeholder="请输入内容">
    <template slot="prepend">Http://</template>
  </fm-input>
</div>
<div class="input-demo">
  <fm-input placeholder="请输入内容">
    <template slot="append">.com</template>
  </fm-input>
</div>
<div class="input-demo">
  <fm-input placeholder="请输入内容" clearable="">
    <template slot="prepend">Http://</template>
    <template slot="append">.com</template>
  </fm-input>
</div>
</div>
                                                <p>可通过 slot 来指定在 input 中前置或者后置内容</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"input-demo"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span>Http://<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"input-demo"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"input-demo"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">fm-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">clearable</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span>Http://<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">fm-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="shu-xing"><a class="header-anchor" href="#shu-xing" aria-hidden="true">¶</a> 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定值</td>
<td style="text-align:left">string/number</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">输入框尺寸</td>
<td style="text-align:left">string</td>
<td style="text-align:left">large/medium/small/mini</td>
<td style="text-align:left">medium</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">placeholder</td>
<td style="text-align:left">输入框占位文本</td>
<td style="text-align:left">string</td>
<td style="text-align:left">—</td>
<td style="text-align:left">请输入/Please input</td>
</tr>
<tr>
<td style="text-align:left">clearable</td>
<td style="text-align:left">是否可清空</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">false</td>
</tr>
</tbody>
</table>
<h3 id="slots"><a class="header-anchor" href="#slots" aria-hidden="true">¶</a> Slots</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">name</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">prepend</td>
<td style="text-align:left">输入框前置内容</td>
</tr>
<tr>
<td style="text-align:left">append</td>
<td style="text-align:left">输入框后置内容</td>
</tr>
</tbody>
</table>
<h3 id="shi-jian"><a class="header-anchor" href="#shi-jian" aria-hidden="true">¶</a> 事件</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">事件名称</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">blur</td>
<td style="text-align:left">在 Input 失去焦点时触发</td>
<td style="text-align:left">(event: Event)</td>
</tr>
<tr>
<td style="text-align:left">focus</td>
<td style="text-align:left">在 Input 获得焦点时触发</td>
<td style="text-align:left">(event: Event)</td>
</tr>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">在 Input 值改变时触发</td>
<td style="text-align:left">(value: string | number)</td>
</tr>
</tbody>
</table>
</section></template>
<style>
    .input-demo {
        margin-bottom: 15px;
    }
</style>
<script>
    export default {
        data () {
            return {
                val: '',
                val1: '点击按钮清空'
            }
        },

        watch: {
            val (value) {
                console.log('input value', value);
            }
        }
    }
</script>


